.slick-slider
{
    
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
    margin-top: 90px;
}

.slick-list
{
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;
}

.slick-track
{
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    width: 6075px;
    opacity: 1;
    
    transition: -webkit-transform 65000ms ease 0s;
    -webkit-animation-name: bouncing;
            animation-name: bouncing;
    -webkit-animation-duration: 80000ms;
            animation-duration: 80000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes bouncing {
    0% {
        -webkit-transform: translate3d(-972px, 0px, 0px);
                transform: translate3d(-972px, 0px, 0px); 
    }
    50%{
        -webkit-transform: translate3d(0px, 0px, 0px);
                transform: translate3d(0px, 0px, 0px);
    }
    100%{
        -webkit-transform: translate3d(-972px, 0px, 0px);
                transform: translate3d(-972px, 0px, 0px);
    }

}

@keyframes bouncing {
    0% {
        -webkit-transform: translate3d(-972px, 0px, 0px);
                transform: translate3d(-972px, 0px, 0px); 
    }
    50%{
        -webkit-transform: translate3d(0px, 0px, 0px);
                transform: translate3d(0px, 0px, 0px);
    }
    100%{
        -webkit-transform: translate3d(-972px, 0px, 0px);
                transform: translate3d(-972px, 0px, 0px);
    }

}

.slick-track--active{
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
}

.slick-track__slick{
    width: 243px;

}

