    padding-bottom:20px;
}

.footer__logo{
    width: 150px;
}

.footer__title{
    color: #969da1;
    font-family: poppins,Sans-serif;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.2em;
    letter-spacing: 3.68px;
}

.footer__content{
    margin-top: 30px;
    font-size: 16px;
}

.copy{
    color: #fff;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.6;
    letter-spacing: 0;
    text-align: left;
    margin-bottom: 16px;
}

.eta{
    color: #4fd2ab;
    font-size: 16px;
    font-weight: 700;
    margin-right: 3px;
}

.footer span:hover{
    color: #4fd2ab;
    cursor: pointer;
}

.footer__icon{
    color: #969da1;
    font-size: 16px;
}

.a-cus{
    color: #969da1;
}

.ic-cont{
    padding: 8px;
    padding-left: 0;
    margin-right: 13px;
    cursor: pointer;
}

.copy-right{
    margin-top: 30px;
    color: rgb(255, 255, 255);
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.6;
    font-family: poppins, sans-serif;
    letter-spacing: 0px;
    text-align: left;
    border-style: solid;
    border-width: 1px 0px 0px;
    border-color: rgba(255, 255, 255, 0.1);
    /* transition: background 0.3s ease 0s, border 0.3s ease 0s, border-radius 0.3s ease 0s, box-shadow 0.3s ease 0s; */
    padding-top: 20px;
    padding-bottom: 10px;
}
.sticky_icon{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 55px;
    height: 55px;
    position: fixed;
    z-index: 16;
    background-color: #192a56;
    border-radius: 50%;
    bottom: 28px;
    right: 20px;
    cursor: pointer;
}

@media (max-width: 766px){
    .sticky_icon{
        width: 40px;
        height: 40px;
        bottom: 28px;
        right: 8%;
        background-color: transparent
    }

    .sticky_icon img {
        width: 100%;
        height: 100%;
    }
}




@media (max-width: 766px) {
    .columns-wrapper{
        max-width: 60vw;
        flex-direction: column;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        
    }

    .columns-wrapper .column{
        width: 100%;
        margin-bottom: 20px;
    }

    

    .section2 .we_know_style{
        font-size: 26px;
    }

    
    .question-list{
        flex-direction: column;
        align-items: center;
        padding-left: 0!important;
        padding-right: 0!important;
    }


    .three-tricks__item{
        padding: 10px 15px;
    }

    .question-list .question-list__left, .question-list .question-list__right{
        width: 80%;

    }
    .three-tricks__item .title{
        font-size: 20px;
    }

    .three-tricks__image img{
        width: 200px;
    }

    .container_cs{
        max-width: 80vw!important;
    }
}

@media (max-width: 566px){
    .three-tricks__image img{
        width: 300px;
    }
    .three-title{
        font-size: 2rem!important;
    }

    .container_cs{
        max-width: 90vw!important;
    }

    .three-tricks{
        flex-direction: column;
    }

    .three-tricks .three-tricks__item{
        width: 100%!important;
        min-height: 350px!important;
        margin-bottom: 20px;
        padding-bottom: 40px;
        padding: 10px 40px;
    }

    

    .question-list .question-list__left, .question-list .question-list__right{
        width: 95%;

    }

    .columns-wrapper{
        max-width: 90vw;
        
    }
    .the_trust{font-size:22px}

    .we_have{
        font-size:14px;
        width:100%;
        margin-top:10px
    }

}

